#snav {
    box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);

    .mat-drawer-inner-container {
      overflow: hidden;
    }

    .mat-nav-list .mat-list-item {
      height: auto;

      &:hover {
        background: none;
      }

      .mat-list-item-content {
        display: block;
        padding: 0px 0px;

        &:hover {
          background: none;

          > a {
            color: $themecolor;
          }
        }
      }

      a {
        height: 45px;
        padding: 10px 10px;
        margin-bottom: 10px;
        align-items: center;
        display: block;
        font-size: 15px;
        white-space: nowrap;
        color: $sidebar-text;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        mat-icon:not(.dd-icon) {
          margin-right: 8px;
        }

        mat-icon {
          color: $sidebar-icons;
        }

        .dd-icon {
          font-size: 16px;
          width: 16px;
          transition: 0.2s ease-in;
          margin: 5px 0 5px 5px;
          height: 16px;
        }
      }

      &.selected > .mat-list-item-content > a {
        background: $themecolor-alt;
        border-radius: $radius;
        color: $white;

        mat-icon {
          color: $white;
        }

        .dd-icon {
          -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
        }
      }

      &.selected .sub-item .mat-list-item-content a.selected {
        /*background: transparent;*/
        background: #eef5f9;
        color: $inverse;
        font-weight: 500;
      }
    }

    .app-sidebar > .mat-nav-list > .mat-list-item > .mat-list-item-content {
      padding: 0px 15px;
    }

    .mat-nav-list .sub-item {
      display: none;
      margin-top: -15px;

      a {
        padding-left: 40px;
        height: 45px;
        margin-bottom: 0px;
      }

      .child-sub-item {
        display: none;
        margin-top: -15px;

        a {
          padding: 0 0 0 55px;
          height: 45px;
          margin-bottom: 0px;
        }
      }
    }

    .saperator {
      cursor: text;
      font-size: 13px;
      margin-bottom: 15px;
      margin-top: 25px;
      font-weight: 500;
      white-space: nowrap;
    }

    .selected {
      .sub-item {
        display: block;

        .selected + {
          .child-sub-item {
            display: block;
          }
        }
      }
    }
  }

/*******************
use profile section
******************/

.user-profile {
    position: relative;
    background-size:cover;

    .profile-img {
        width: 50px;
        margin-left: 30px;
        padding: 31px 0;
        border-radius: 100%;
        &::before {
            -webkit-animation: 2.5s blow 0s linear infinite;
            animation: 2.5s blow 0s linear infinite;
            position: absolute;
            content: '';
            width: 50px;
            height: 50px;
            top: 31px;
            margin: 0 auto;
            border-radius: 50%;
            z-index: 0;
        }
        @-webkit-keyframes blow {
            0% {
                box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);
                opacity: 1;
                -webkit-transform: scale3d(1, 1, 0.5);
                transform: scale3d(1, 1, 0.5);
            }
            50% {
                box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);
                opacity: 1;
                -webkit-transform: scale3d(1, 1, 0.5);
                transform: scale3d(1, 1, 0.5);
            }
            100% {
                box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.1);
                opacity: 0;
                -webkit-transform: scale3d(1, 1, 0.5);
                transform: scale3d(1, 1, 0.5);
            }
        }
        @keyframes blow {
            0% {
                box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);
                opacity: 1;
                -webkit-transform: scale3d(1, 1, 0.5);
                transform: scale3d(1, 1, 0.5);
            }
            50% {
                box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);
                opacity: 1;
                -webkit-transform: scale3d(1, 1, 0.5);
                transform: scale3d(1, 1, 0.5);
            }
            100% {
                box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.1);
                opacity: 0;
                -webkit-transform: scale3d(1, 1, 0.5);
                transform: scale3d(1, 1, 0.5);
            }
        }
        img {
            width: 100%;
            border-radius: 100%;
        }
    }
    .profile-text {
        padding: 5px 0px;
        position: relative;
        cursor: pointer;
        white-space: nowrap;
        > a {
            color: $white!important;
            width: 100%;
            padding: 6px 30px;
            background: rgba(0, 0, 0, 0.5);
            display: block;
            white-space: nowrap;
            &:after{
                position: absolute;
                right: 20px;
                top:20px;
            }
        }
    }

}


.sidebar-closed #snav{
    &.mat-sidenav ~ .mat-drawer-content {
          margin-left: 0px!important;
        }
}
